.container > div {
    padding: 10px;
    text-align: center;
    font-size: 2em;
    color: #ffeead;
}

html, body {
    background-color: #ffeead;
    margin: 10px;
    width: 90%;
}

.container > div:nth-child(2n-1){
    background-color: #96ceb4;
}

.container > div:nth-child(2n){
    background-color: #ff6f69;
}
